import { upload } from "./upload"
import "./theme.scss"
import { initializeApp } from "firebase/app"
import { getStorage, ref, uploadBytesResumable, getDownloadURL } from "firebase/storage"


// Firebase
const firebaseConfig = {
  apiKey: "AIzaSyCKOWHWegL8l61iwDterqRTaPvGUYwDiGU",
  authDomain: "file-upload-99fb5.firebaseapp.com",
  projectId: "file-upload-99fb5",
  storageBucket: "file-upload-99fb5.appspot.com",
  messagingSenderId: "553975129106",
  appId: "1:553975129106:web:1eb8a44ba260b9876364cb"
};
// Initialize Firebase
initializeApp(firebaseConfig);



upload('#file', {
  multiple: true,
  accept: [".png", ".jpg", ".jpeg", ".gif"],
  onUpload(files, blocks) {
    files.forEach((file, index) => {
      const storage = getStorage()
      const storageRef = ref(storage,`images/${file.name}`)
      const task = uploadBytesResumable(storageRef, file)

      task.on("state_changed", snapshot => {
        const progress = ((snapshot.bytesTransferred / snapshot.totalBytes) * 100).toFixed(0)
        const block = blocks[index].querySelector(".preview-info-progress")
        block.textContent = progress
        block.style.width = progress + "%"
      }, error => {
        console.log(error);
      }, complete => {
        getDownloadURL(task.snapshot.ref).then((downloadURL) => {
          console.log(`${file.name}[${index}] available at`, downloadURL);
        });
      })
    })
  }
})